function extractTags(requirements: string): string[] {
  const commonTags = ['web', 'mobile', 'api', 'database', 'auth', 'integration', 'analytics', 'ai', 'ml', 'cloud']
  const tags: string[] = []
  
  commonTags.forEach(tag => {
    if (requirements.toLowerCase().includes(tag)) {
      tags.push(tag)
    }
  })
  
  return tags.slice(0, 5) // Max 5 tags
}
